export class procedure {
  //ÂüéÂå∫ËøòÊòØÂâØÊú¨
  private evAF: eventArea;
  //‰ªªÂä°ÊèèËø∞
  private evDis: string = "";
  //‰ªªÂä°ÂØπË±°Êé•Êî∂‰∫∫
  private evwho: string;
  //‰ªªÂä°Âú∞ÁÇπ
  private evPos: string;
  //ÂÆåÊàêÁõÆÊ†á
  private evGoal: goal[];
  //‰ªªÂä°Á±ªÂûã
  private evType: eventType;
  //ÂàÜÊîØ‰ªªÂä°Áä∂ÊÄÅ
  private evTaskSts: boolean = false;

  constructor(
    evT: eventType,
    evAF: eventArea,
    evW: string,
    evP: string,
    evGoal: goal[]
  ) {
    this.evType = evT;
    this.evwho = evW;
    this.evPos = evP;
    this.evGoal = evGoal;
    this.evAF = evAF;
    this.update(evP, evW, evT, evGoal);
  }
  update(evP: string, evW: any, evT: eventType, evGoal: goal[]) {
    let dis;

    this.evDis = "Âú∞ÁÇπÔºö" + evP + ",ÂØπË±°Ôºö" + evW + ",‰ªªÂä°Ôºö";
    switch (evT) {
      case eventType.talk:
        this.evDis +=
          "‰∏é" +
          evW +
          "‰ºöËØù(" +
          evGoal[0].getFinNumN() +
          "/" +
          evGoal[0].getFinNumG() +
          ")";
        break;
      case eventType.collect:
        dis = this.toString(evGoal);
        this.evDis += "Êî∂ÈõÜ" + dis;
        break;
      case eventType.brush:
        dis = this.toString(evGoal);
        this.evDis += "Ê∏ÖÈô§" + dis;
        break;
      case eventType.make:
        dis = this.toString(evGoal);
        this.evDis += "Âà∂" + dis;
        break;
    }
  }
  toString(goals: goal[]) {
    let tmp = "[";
    for (let i = 0; i < goals.length; i++) {
      tmp +=
        goals[i].getName() +
        ":" +
        goals[i].getFinNumN() +
        "/" +
        goals[i].getFinNumG() +
        ",";
    }
    tmp = tmp.substring(0, tmp.length - 1);
    tmp += "]";
    return tmp;
  }
  getTotal() {
    let len = 0;
    for (let i = 0; i < this.evGoal.length; i++) {
      len += this.evGoal[i].getFinNumG();
    }
    return len;
  }
  setEvAF(evaf: eventArea) {
    return (this.evAF = evaf);
  }
  getEvAF() {
    return this.evAF;
  }
  setEvType(t: eventType) {
    this.update(this.evPos, this.evwho, t, this.evGoal);
    return (this.evType = t);
  }
  setEvWho(w: string) {
    this.update(this.evPos, w, this.evType, this.evGoal);
    return (this.evwho = w);
  }
  setEvGoal(g: goal[]) {
    return (this.evGoal = g);
  }
  getEvGoal() {
    return this.evGoal;
  }
  setEvTS(s: boolean) {
    return (this.evTaskSts = s);
  }
  getEvTS() {
    return this.evTaskSts;
  }
  getEvType() {
    return this.evType;
  }
  getEvWho() {
    return this.evwho;
  }
  setPos(pos: string) {
    this.evPos = pos;
    this.update(pos, this.evwho, this.evType, this.evThing);
  }
  getDis() {
    return this.evDis;
  }
}
export class event {
  //‰ªªÂä°ÁºñÂè∑
  private evNum: number = cnt;
  //ÂèëËµ∑‰∫∫
  private proPerson: any;
  //‰ªªÂä°Èò∂ÊÆµ
  private stage: number = 0;
  //ÂèëËµ∑ÂéüÂõ†
  private proReason: string;
  //Â•ñÂä±
  private prize: any[];
  //ÊâßË°åËøáÁ®ã
  private process: procedure[];
  // ‰ªªÂä°Áä∂ÊÄÅ
  private evSts = false;

  constructor(person: any, res: string, process: procedure[]) {
    this.proPerson = person;
    this.proReason = res; //this.proPerson + "ÊÉ≥" + res
    this.process = process;
    this.prize = [];
    //ËÆ°ÁÆóÈáëÂ∏ÅÂ•ñÂä±
    for (let i = 0; i < process.length; i++) {
      //ÂüéÂ§ñ
      if (process[i].getEvAF() == eventArea.outside) {
        if (process[i].getTotal() <= 3) {
          this.add(4);
        } else if (process[i].getTotal() <= 6) {
          this.add(10);
        } else if (process[i].getTotal() <= 9) {
          this.add(14);
        } else {
          this.add(16);
        }
      } else {
        //ÂüéÂÜÖ
        if (process[i].getTotal() <= 2) {
          this.add(1);
        } else {
          this.add(3);
        }
      }
    }
    cnt++;
  }
  add(num: number) {
    for (let i = 1; i <= 3; i++) {
      this.prize.push(num + i + 1);
    }
  }
  setEvSts(S: boolean) {
    return (this.evSts = S);
  }
  getProPerson() {
    return this.proPerson;
  }
  getProReason() {
    return this.proReason;
  }
  getPrize() {
    return this.prize;
  }
  getStage() {
    return this.stage;
  }
  getProcess() {
    return this.process;
  }
  getEvNum() {
    return this.evNum;
  }
  setProcess(p: any) {
    return (this.process = p);
  }
  setPrize(p: any) {
    return (this.prize = p);
  }
  setStage(i: number) {
    return (this.stage = i);
  }
  setProPerson(p: any) {
    return (this.proPerson = p);
  }
  setProposeReson(s: string) {
    return (this.proReason = s);
  }
}
export class EveMag {
  private pool: event[];
  constructor() {
    this.pool = [];
  }
  getPool() {
    return this.pool;
  }
  addEvent(ev: event) {
    this.pool.push(ev);
  }
  FinishEvennt(n: number) {
    for (let i = 0; i < this.pool.length; i++) {
      if (this.pool[i].getEvNum() == n) {
        this.pool[i].setEvSts(true);
        return this.pool.splice(i, 1);
      }
    }
    return false;
  }
}
